export class Pair<T,U>
{
    private item1:T
    private item2: U;
    constructor(one:T, two:U){
        this.item1 = one;
        this.item2 = two;
    }

    get Item1():T{return this.item1;}
    set Item1(item: T){this.item1 = item;}

    get Item2():U{return this.item2;}
    set Item2(item:U){this.item2 = item;}
}