import * as React from "react";
import { HomeViewModel } from "../../cms/home/HomeViewModel";


type HomeProps = {}
type HomeState = {title: string}


export class Home extends React.Component<HomeProps, HomeState> {
    
    model: HomeViewModel;
    constructor(props: HomeProps){
        super(props);

        this.state = {title: "Home"}
        this.model = new HomeViewModel();
    }


    render() {
        return (<div className="container">

                    <div className="row">
                       <div className="col-lg-12">
                            <h1>{this.state.title}</h1>
                       </div>       
                    </div>

                    <div className="row">

                        <div className="col-lg-4">
                           
                           <div className="row center">
                                <div className="col-lg-12">
                                    <img className="img-fluid rounded-circle" src={this.model.Profile}/>
                                </div>
                           </div>

                            <div className="row center">
                                 <div className="col-lg-12">
                                     <a target="_blank" href={this.model.CV} className="btn btn-lg btn-success">Curricilum Vitae</a>
                                 </div>
                           </div>

                        </div>

                        <div className="col-lg-4">
                           <div className="card">

                            <div className="card-header">{this.model.SkillsSection.Item1}</div>

                            <ul className="list-group">
                               {this.model.SkillsSection.Item2.map((skill) => <li className="list-group-item">{skill}</li>)}
                            </ul>
       
                            </div>
                        </div>

                        <div className="col-lg-4">
                             <div className="card">
                                    <div className="card-header">{this.model.CurrentStatus.Item1}</div>

                                    <div className="card-body">
                                       {this.model.CurrentStatus.Item2}
                                    </div>

                             </div>
                        </div>

                    </div>

                    <div className="row">

                        <div className="col-lg-8">
                            <div className="card">
                                     <div className="card-header">{this.model.News.Item1}</div>

                                     <div className="card-body">
                                     {this.model.News.Item2}
                                     </div>

                            </div>
                        </div>

                        <div className="col-lg-4">

                        </div>

                    </div>
            

               </div>);
        


    }
}