import * as React from "react";
import { Link } from "react-router-dom";


export interface NavigationProps { }
export type NavigationState = {menuIn: string }


export class Navigation extends React.Component<NavigationProps, NavigationState> {
  constructor(props: NavigationProps){
    super(props);
    this.state = {menuIn: "Show Menu"};
    this.toggleMenu.bind(this);
  }

  toggleMenu = function():void{
    console.log("clicked");
    if (this.state.menuIn == "Show Menu"){
      this.setState({...this.state, menuIn: "Hide"})
    }else{
      this.setState({...this.state, menuIn: "Show Menu"})
    }
  }

    render() {
        return (  <header>
            <nav className="navbar navbar-expand-sm bg-primary">

              <Link className="navbar-brand" to="/">Steven Koerts</Link>

              <button onClick={() => this.toggleMenu()} className="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
                 <span className="navbar-toggler-icon">{this.state.menuIn}</span>
              </button>
               
              <div className="collapse navbar-collapse" id="collapsibleNavbar">
                  <ul className="navbar-nav">
                    <li className="nav-item">
                        <Link className="nav-link" to='/'>Home</Link>
                    </li>
                    <li className="nav-item">
                       <Link className="nav-link" to='/about'>About</Link>
                    </li>
                    <li className="nav-item">
                       <Link className="nav-link" to='/contact'>Contact</Link>
                    </li>
                   </ul>
              </div>

            </nav>
          </header>);
    }
}