import * as React from "react";
import { Navigation } from "./Navigation";
import { Routes } from "./Routes";
import { Footer } from "./Footer"

export type AppProps = { }
export type AppState = {title: string}

const Props = {title1: "Steven Koerts", title2: "Made with React!!!"}

// 'HelloProps' describes the shape of props.
// State is never set so we use the '{}' type.
export class App extends React.Component<AppProps, AppState> {
    constructor(props: AppProps){
        super(props);
        this.state = {title: "Steven Koerts"}
    }

    changeTitle = function():void{
        setInterval(()=>{
            if (this.state.title == Props.title2){
                this.setState({...this.state, title: Props.title1});
            }else{
                this.setState({...this.state, title: Props.title2});
            }
            
            document.getElementsByTagName("title")[0].text = this.state.title;
            
        }, 800);
    }

    componentWillMount(){
        this.changeTitle();
    }

    render() {
        return (<div>
            
                <Navigation/>
                <Routes/>
                <Footer/>
                
               </div>);
        


    }
}